<template lang="pug">
div
  Navbar
  .container-fluid.pt-5
    .row
      //根據頁面插入對應標題(title)
      Sidebar(:new-title="SassColorGuide" v-if="SassColorGuide.titleText")
      div(class="col offset-lg-3 offset-0 bg-light" style="min-height: 100vh;")
        .container(class="p-5")
          SassColorGuide
</template>

<script>
import Navbar from './Navbar'
import Sidebar from './Sidebar'
import SassColorGuide from './Pages/SassColorGuide'

export default {
  components: {
    Navbar,
    Sidebar,
    SassColorGuide
  },
  data () {
    return {
      SassColorGuide: {
        titleText: 'SASS 色卡工具',
        subText: 'COLOR GUIDE<br>HTML COLOR CODES',
        bgColor: '#FDBBC9'
      }
    }
  }
}
</script>



