<template lang="pug">
.row
  div(class="col-sm-6 col-md-4 col-lg-3 p-3" v-for="(c, key) in colorsheet")
    .removeArea
      .removeBtn(@click.prevent="removeColor(key)")
        i(class="fas fa-times")
    .color-card.rounded(:id="c.colorName" @click.prevent="copyColorCode(c)")
      .color-block(:style="{backgroundColor: c.colorCode}")
      .color-info(class="bg-white pl-2 py-2" @dblclick.prevent="editColor(c)")
        .color-name {{ c.colorName.toUpperCase() }}
        .color-code {{ c.colorCode.toUpperCase() }}
  div(class="addContainer" tabindex="1")
      input(:class="validColorNameStyle" type="text" class="form-control mr-3" placeholder="變數名稱" v-model="newColorName")
      input(:class="validColorHexStyle" type="text" class="form-control" placeholder="#DDC9F2" v-model="newColorCode")
      .addBtn(@click.prevent="addColor()")
        i(class="fas fa-plus")
</template>

<script>
import $ from 'jquery'

export default {
  name: 'SassColorGuide',
  data () {
    return {
      colorinit: `
        $color_bg: #E0CAD0;
        $color_pink: #CE7597;
        $color_pinkL: #FDBBC9;
        $color_gray: #AAB7C6;
        $color_top: #9C4215;
        $color_md_1: #F3D1BA;
        $color_md_2: #F7E1CD;
        `,
      colorsheet: [],
      tempColor: {},
      newColorName: '',
      newColorCode: ''
    }
  },
  methods: {
    strCopy (v) {
      let $temp = $('<textarea>')
      $('body').append($temp)
      $temp.val(v).select()
      document.execCommand('Copy')
      $temp.remove()
    },
    copyColorCode (c) {
      // Copy Color Code
      this.tempColor = Object.assign({}, c)
      this.strCopy(this.tempColor.colorCode)

      // Text Animation
      let copyColor = $(`#${this.tempColor.colorName} .color-block`)
      copyColor.append('<div class="colorActive animated fadeOut">')
      setTimeout(() => copyColor.empty(), 1000)
    },
    addColor () {
      if (!this.validColorName || !this.validColorHex) {
        return
      }
      this.colorsheet.push({
        colorName: this.newColorName,
        colorCode: this.newColorCode
      })
      this.newColorName = ''
      this.newColorCode = ''
    },
    removeColor (key) {
      console.log(key)
      this.colorsheet.splice(key, 1)
    },
    editColor (c) {
      this.tempColor = Object.assign({}, c)
      console.log('edit')
    }
  },
  computed: {
    colorInit () {
      let result = []
      this.colorinit.split('\n').forEach(
        (obj, index) => {
          if (obj.indexOf('color') !== -1) {
            result.push({
              colorName: obj.split(':')[0].split('$')[1],
              colorCode: obj.split(':')[1].trim().split(';')[0]
            })
          }
        }
      )
      return result
    },
    validColorName () {
      return this.newColorName.trim() !== ''
    },
    validColorNameStyle () {
      return (this.newColorName === '') ? '' : this.validColorName ? '' : 'is-invalid'
    },
    validColorHex () {
      let hexRegex = /^#.[0-9a-f]+$/
      return hexRegex.test(this.newColorCode)
    },
    validColorHexStyle () {
      return (this.newColorCode === '') ? '' : this.validColorHex ? '' : 'is-invalid'
    }
  },
  created () {
    this.colorsheet = this.colorInit
    this.$bus.$emit('initColorsheet', this.colorsheet)
  }
}
</script>

<style scoped lang="scss">
</style>



