import Vue from 'vue'
import VueRouter from 'vue-router'

// import Home from '@/components/SassColorGuide'
import WebTools from '@/components/WebTools'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      name: '首頁',
      path: '/',
      component: WebTools
    },
    {
      name: 'WebTools',
      path: '/webtools',
      component: WebTools
    }
  ]
})



