<template lang="pug">
  div#sidebar(class="col-md-3 d-none d-lg-block p-0")
    .top(class="position-relative" :style="{backgroundColor: title.bgColor}")
      .title(class="pl-4 pb-3")
        h5(v-html="title.subText")
        h1.m-0(v-html="title.titleText")
    .bottom(class="d-flex flex-column bg-white")
      SidebarSass
</template>

<script>
import SidebarSass from './Sidebar_sass'

export default {
  components: {
    SidebarSass
  },
  props: ['newTitle'],
  data () {
    return {
      title: this.newTitle
    }
  },
  created () {
  }
}
</script>

<style scoped lang="scss">
h1, h5 {
  font-family: 'Playfair Display', 'source-han-serif-tc';
  font-weight: 600;
  cursor: default;
}

h1 {
  color: white;
}

h5 {
  opacity: 0.3;
}

.title {
  position: absolute;
  left: 0;
  bottom: 0;
}
</style>



